@import "../../theme.scss";

.photoalbum {
  height: 100%;
  display: flex;
  flex-direction: column;

  .create-photoalbum {
    height: 66px;
    padding: 0 15px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    border: 1px solid rgba(235, 235, 235, 1);
    display: flex;

    .create-photoalbum-btn {
      align-self: center;

      button {
        padding: 0 20px;
        height: 40px;
        background: $bg-color;
        border-radius: 3px;
        border: 0;
        font-size: 14px;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 20px;
        cursor: pointer;
      }
    }

    .photoalbum-num {
      align-self: center;
      margin-left: 50px;

      .photoalbum-num-icon {
        height: 100%;
        display: inline-block;
        font-size: 32px;
        vertical-align: middle;
        margin-right: 10px;
        padding-top: 5px;
        i {
          font-size: 32px;
        }
      }

      .num-text {
        float: right;
        height: 100%;
        padding: 5px 3px 0 3px;
        text-align: left;

        .text {
          margin: 0;
          height: 19px;
          font-size: 13px;
          font-weight: 400;
          color: rgba(1, 1, 1, 0.6);
          line-height: 19px;
        }

        .num {
          margin: 0;
          height: 24px;
          font-size: 16px;
          font-weight: 400;
          color: rgba(25, 25, 25, 1);
          line-height: 24px;
        }
      }
    }
  }

  .photoalbum-operation {
    display: flex;
    height: 45px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    border: 1px solid rgba(235, 235, 235, 1);
    padding: 0 15px;

    .checkAll {
      align-self: center;
      display: flex;

      .check-photoalbum {
        margin-right: 10px;
        cursor: pointer;
        font-size: 20px;
      }
      .icon-xuanzhong1{
        color: $bg-color;
      }

      .check-photoalbum-opt {
        span {
          background: $bg-color;

          i {
            visibility: initial;
          }
        }
      }

      .checkAll-text,
      .invert-Selection {
        font-size: 14px;
        color: rgba(2, 2, 2, 1);
        line-height: 20px;
        margin-right: 24px;
      }

      .invert-Selection {
        cursor: pointer;
        color: $bg-color;
      }
    }

    .photoalbum-sort {
      position: relative;
      height: 30px;
      align-self: center;
      border-left: 1px solid #e9e9e9;
      padding-left: 24px;
      margin-right: 24px;
      display: flex;

      .sort {
        align-self: center;
        border-radius: 3px;
        border: 1px solid rgba(191, 191, 191, 1);
        padding: 0 10px;
        height: 100%;
        line-height: 30px;
        cursor: pointer;

        i {
          margin-left: 10px;
        }

        &:hover {
          background-color: $hover;
        }
      }

      .sort-list {
        width: 250px;
        position: absolute;
        top: 31px;
        right: 0;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.15);
        border-radius: 3px;
        border: 1px solid rgba(233, 233, 233, 1);
        padding: 12px 0;
        z-index: 9;

        ul li {
          cursor: pointer;
        }

        ul li span {
          display: block;
          height: 30px;
          font-size: 14px;
          color: rgba(2, 2, 2, 1);
          line-height: 30px;
          padding-left: 15px;

          i {
            visibility: hidden;
            margin-right: 15px;
          }

          .check-icon {
            visibility: initial;
            color: $bg-color;
          }
        }

        ul li:hover {
          background: $hover;
        }
      }
    }

    .photoalbum-delete {
      align-self: center;
      padding-left: 24px;
      border-left: 1px solid #e9e9e9;

      span {
        display: block;
        height: 30px;
        padding: 0 10px;
        border-radius: 3px;
        border: 1px solid rgba(191, 191, 191, 1);
        font-size: 12px;
        font-weight: 400;
        color: rgba(31, 31, 31, 1);
        line-height: 30px;
        cursor: pointer;

        i {
          margin-right: 10px;
        }
      }
    }
  }

  .photoalbum-main {
    flex: 1;
    overflow-y: auto;
    padding-bottom: 135px;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;

    .list {
      padding: 5px 10px;
    }

    .album_list {
      position: relative;
      display: flex;
      flex-direction: column;
      height: 100%;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 2px 6px 0px rgba(235, 235, 235, 1);
      border: 2px solid rgba(255, 255, 255, 1);
      border-radius: 4px;
      cursor: pointer;

      .albumlist-check {
        position: absolute;
        display: block;
        visibility: hidden;
        top: 5px;
        right: 5px;
        font-size: 20px;
        background: rgba(255, 255, 255, 1);
        border-radius: 5px;
        z-index: 1;
        color: $icon;
      }

      .top-img {
        height: 50%;
        background-color: #f3f5f7;
        background-repeat: no-repeat;
        background-position: center;

        img {
          height: 100%;
          width: 100%;
          object-fit: cover;
          border-radius: 2px 2px 0 0;
        }
      }

      .bottom-information {
        height: 50%;
        display: flex;
        flex-direction: column;
        padding: 0 14px;

        .name-time {
          height: 50%;

          .album-name {
            width: 150px;
            height: 19px;
            font-size: 14px;
            color: rgba(0, 0, 0, 1);
            line-height: 19px;
            margin: 11px 0;
          }

          .update-time {
            overflow: hidden;
            margin-bottom: 10px;

            span {
              display: block;
              float: left;
              height: 21px;
              background: rgba(243, 243, 243, 1);
              border-radius: 3px;
              font-size: 12px;
              font-weight: 400;
              color: rgba(144, 144, 144, 1);
              line-height: 19px;
              padding: 0 7px;
            }
          }
        }

        .photoNum-more {
          height: 50%;
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 12px 0;
          border-top: 1px solid #e7e7e7;

          p {
            margin: 0;
          }

          .photo-num {
            float: left;
            padding: 0 5px;
            text-align: center;
          }

          .more {
            position: relative;

            span {
              font-size: 20px;
              color: #000;
              padding: 0 3px;
              border-radius: 3px;

              &:hover {
                background-color: $hover;
              }
            }

            .rename {
              position: absolute;
              display: none;
              top: 33px;
              right: -20px;
              width: 180px;
              background: rgba(255, 255, 255, 1);
              box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.15);
              border-radius: 3px;
              padding: 12px 0;
              border: 1px solid rgba(233, 233, 233, 1);
              z-index: 9;

              ul li span {
                height: 42px;
                line-height: 42px;
                font-size: 14px;
                color: rgba(2, 2, 2, 1);

                i {
                  font-size: 20px;
                  margin: 0 16px;
                  vertical-align: middle;
                }
              }

              ul li:hover {
                background-color: $hover;
              }
            }
          }

          .num1 {
            height: 20px;
            font-size: 14px;
            font-weight: 500;
            color: rgba(25, 25, 25, 1);
            line-height: 20px;
          }

          .text1 {
            height: 19px;
            font-size: 13px;
            font-weight: 400;
            color: rgba(25, 25, 25, 1);
            line-height: 19px;
            opacity: 0.3;
          }
        }
      }

      .check-FF6627 {
        background: $bg-color;
        border: 2px solid $bg-color;
        visibility: initial;

        i {
          visibility: initial;
        }
      }

      &:hover .albumlist-check {
        visibility: initial;
      }

      &:hover {
        border: 2px solid $bg-color;
      }
    }

    .album_list-check {
      border-color: $bg-color;

      .albumlist-check {
        visibility: initial;
      }
    }
  }

  .created-modul {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.2);
    overflow: hidden;
    z-index: 99;

    .created-warp {
      width: 500px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.15);
      border-radius: 3px;

      .created-warp-header {
        display: flex;
        justify-content: space-between;
        padding: 15px 24px;
        border-bottom: 1px solid #e9e9e9;

        h3,
        span {
          height: 24px;
          font-size: 18px;
          color: rgba(0, 0, 0, 1);
          line-height: 24px;
          margin: 0;
        }

        span {
          cursor: pointer;
        }
      }

      .created-warp-boby {
        padding: 0 24px;

        .created-warp-input {
          margin: 24px 0;

          input {
            padding-left: 12px;
            width: 452px;
            height: 48px;
            background: rgba(246, 246, 246, 1);
            border-radius: 3px;
            border: 1px solid $bg-color;
            font-size: 14px;
            font-weight: 400;
            color: rgba(0, 0, 0, 1);
            line-height: 20px;
          }
        }

        .created-warp-hanle {
          text-align: right;
          padding: 22px 0;

          .cancel-btn,
          .confirm-btn {
            display: inline-block;
            padding: 0 33px;
            height: 40px;
            font-size: 14px;
            font-weight: 400;
            color: rgba(10, 10, 10, 1);
            line-height: 40px;
            border-radius: 3px;
            border: 1px solid rgba(194, 196, 199, 1);
            cursor: pointer;
          }

          .confirm-btn {
            margin-left: 24px;
            background: $bg-color;
            color: rgba(255, 255, 255, 1);
          }
        }
      }
    }
  }
}